.App-logo {
  height: 10vmin;
  pointer-events: none;
  padding: 5px;
}

h1 {
  font-size: 1.3rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 18s linear;
  }
}

.App-header {
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
  color: white;
  display: flex;
  align-items: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.contPicture {
  display: flex;
  justify-content: center;
}

.profilePicture {
  width: 250px;
  height: auto;
  border-radius: 20px;
  margin-top: 20px;
}

.infosCont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}

h2 {
  margin-bottom: 0px;
}
 
h3 {
  font-size: 15px;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.btn {
  border-radius: 50px;
  padding: 10px 20px;
  border: none;
  background-color: rgb(0, 225, 255);
  margin-bottom: 20px;
  font-weight: bold;
}

.btn:hover {
  cursor: pointer;
  background-color: rgb(136, 241, 255);
  transition: 0.4s;
}

li {
  padding: 5px;
}

li::marker {
  color: aqua;
  font-size: 1.3rem;
}